  formValid = async (item) => {
    let sendData = {
      owner_id: Number(this.state.userId),
      sub_id: Number(item.id),
    };
    let result = await HttpClient.requestData(
      "app-owner/subscription/buy-now",
      "POST",
      sendData
    );
    if (result && result.status > 0) {
      this.setState({
        alert: true,
        message: "Plan Subscribed Successfully",
        type: "success",
      });
      this.fetchActivePlans();
      this.fetchPlans();
    } else {
      this.setState({ alert: true, message: result.error, type: "danger" });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  imageUpload = async (e) => {
    // console.log("e", e.target.files);
    let file = e.target.files;
    //   if(file.lenght>0)
    //   {
    let dataSend = { buckate: true, buckate_name: "appIcon" };

    let result = await HttpClient.newFileUpload(
      "image-upload/profile",
      e.target.files[0],
      dataSend
    );
    console.log("result", result);
    if (result && result.status) {
      this.setState({ image: result.url, image_select: true });
    }

    //   }
  };

  submit = async () => {
    if (
      this.state.name != "" &&
      this.state.email != "" &&
      this.state.mobile != "" &&
      this.state.image != ""
    ) {
      let data = null;
      let result = null;
      // return false;
      data = {
        name: this.state.name,
        email: this.state.email,
        mobile: this.state.mobile,
        address: this.state.address,
        website: this.state.website,
        organizerInformation: this.state.organizerInformation,
      };
      console.log("data", data);
      result = await HttpClient.requestData(
        "admin/organizer/" + this.state.edit_id,
        "PUT",
        data
      );

      if (result && result.status) {
        this.setState({
          alert: true,
          message: this.state.edit_enable
            ? "Profile Updated Successfully"
            : "Profile Added Successfully",
          type: "success",
        });

        this.fetchUser();
      } else {
        this.setState({
          alert: true,
          message:
            typeof result.error == "string"
              ? result.error
              : result.error[0].msg,
          type: "danger",
        });
      }
    } else {
      this.setState({
        alert: true,
        message: "Please Fill Up All Details.",
        type: "warning",
      });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

  submitPass = async () => {
    if (this.state.new_pass != "" && this.state.con_new_pass != "") {
      if (this.state.new_pass == this.state.con_new_pass) {
        let data = null;
        let result = null;
        // return false;
        data = {
          password: this.state.new_pass,
          id: this.state._id,
        };
        console.log("data", data);
        result = await HttpClient.requestData(
          "admin/update-pass",
          "POST",
          data
        );

        if (result && result.status) {
          reactLocalStorage.setObject("userData", result.data);
          this.setState({
            alert: true,
            message: this.state.edit_enable
              ? "Password Updated Successfully"
              : "Password Added Successfully",
            type: "success",
          });

          this.fetchUser();
        } else {
          this.setState({
            alert: true,
            message:
              typeof result.error == "string"
                ? result.error
                : result.error[0].msg,
            type: "danger",
          });
        }
      } else {
        this.setState({
          alert: true,
          message: "Password Not Matched.",
          type: "warning",
        });
      }
    } else {
      this.setState({
        alert: true,
        message: "Please Enter Password And Confirm Password",
        type: "warning",
      });
    }
    setTimeout(() => {
      this.setState({
        alert: false,
        message: "",
        type: "",
      });
    }, 3000);
  };

